<template>
  <div>
      <col-title>{{data.goods_name}}</col-title>
      		<div class="wrapbox padding-footer">
			<!---->
			<carousel :bannerList='this.data.banner_list'></carousel>
			<div class="xm-title padding20">
				<h1>{{data.intro}}</h1>
				<p>
					<!-- <span class="pull-right place"></span> -->
					<i class="tag tag-baoxian" v-if="data.is_buy_now"></i><i class="tag tag-tehui" v-if="data.is_special_offer"></i></p>
			</div>
			<div class="box-space-top15">
				<div class="xm-pricebox padding20"><span class="pull-right tip-yuyue">{{data.sale_count}}</span><span class="price-now">{{data.price}}</span><span class="price"> ({{data.old_price}})</span><i class="tag-zhekou">{{(data.price/data.old_price).toFixed(2)}}</i></div>
				<div class="xm-dikoubox clearfix">
					<span> <strong>{{data.score}}</strong></span>				
				</div>
			</div>
			
			<!---->
			<ul class="list list-jg">
				<li>
					<h2 class="title" @click="goShopDetail(data.shop_id)">{{data.shop_name}}</h2>
					<p class="qualification">{{data.shop_aptitude}}</p>
					<p class="address">{{data.shop_address}}</p>					
					<div class="btnbox"><button class="btn-icon btn-tel"></button><button class="btn-icon btn-favorate" @click="saveFavor(2,data.shop_id)"></button></div>
					<div class="imgbox" @click="goShopDetail(data.shop_id)"><img :src="data.shop_logo?imgUrl+data.shop_logo:no_img"><i class="mark mark-renzheng" v-if="data.shop_is_verify"></i></div>
				</li>
			</ul>
			<div class="edit-box padding20" v-html="data.description"></div>		
		</div>
		<footer class="footer-bar footer-xm">
			<div class="price pull-left">
				<h3><strong>{{data.price}}</strong></h3>
				<p>{{data.old_price}}</p>
			</div>
			<span class="btn-yuyue pull-right"><button @click="goOrderBuy()"></button></span>
			<span class="btn-shoucang pull-right" @click="saveFavor(1,goods_id)"><button></button></span>			
		</footer>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import Carousel from '../common/carousel'
import { Toast } from 'mint-ui'
export default {
  name:'detail',
  components: {
	  colTitle,
	  Carousel
  },
  data () {
		return {
			data:[],
			goods_id:this.$route.query.id,
			tokenId:localStorage.getItem("tokenId")
		}
	},
	mounted(){
		this.getMainData()
	},
	methods: {
		loadBottom(){
			console.log('sdf');
		},
		getMainData(){
			this.$http.get('/Goods/'+this.goods_id).then(res=>{
				this.data = res.data;
				this.data.banner_list = JSON.parse(this.data.banner_list);
			})
			.catch(err=>{
				this.$router.push('/error')
			})
		},
		goOrderBuy(){
			this.$router.push('/order_buy?id='+this.goods_id)
		},
		goShopDetail(id){
			this.$router.push('/shop_detail?id='+id);
		},
		saveFavor(typeId,itemId)
		{
			if(!this.tokenId)
			{
				Toast({
						message: '',
						position: 'bottom',
						duration: 2000
				});
				return ;
			}

			this.$http.post('/UserFavor',{
					type: typeId,
					itemid: itemId
			},{headers: {"authorization": "Bearer " + this.tokenId}})
			.then(res=>{

				if(res.data.status=="saved")
				{
					Toast({
							message: '',
							position: 'bottom',
							duration: 2000
					});
				}
				else
				{
					Toast({
							message: '',
							position: 'bottom',
							duration: 2000
					});
				}
			})
			.catch(err=>{
				if(err.response.status==401)
				{
					Toast({
						message: '',
						position: 'bottom',
						duration: 2000
					});
				}
				else
				{
					this.$router.push('/error');
				}
			})
			
		}
	}
}
</script>



