<template>
  <div>
    <colTitle></colTitle>
    <div class="wrapbox">
        <div class="empty404">
            <h1>...</h1>
            <button @click="jumpMain"></button>
        </div>
	</div>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
export default {
  name:'error',
  components: {
      colTitle
  },
  methods: {
      jumpMain(){
          this.$router.push('/main')
      }
  }
}
</script>




