<template>
  <div>
      		<header class="header-bar user-header">
			<a class="header-icon header-icon-back pull-left" @click="$router.go(-1)"></a>
			<a class="header-title"></a>
			<a class="header-icon header-icon-setting pull-right"></a>
		</header>
		<div class="wrapbox padding-footer">
			<!---->
			<div class="avatar-box avatar-user">
				<div class="imgbox">
					<!---->
					<img src="img/avatar-default.png" style="display: none;">
					<!---->
					<img :src="data.user_image?imgUrl+data.user_image:no_img">
					<router-link :to="{path:'/user_face',query:{face_img:data.user_image?imgUrl+data.user_image:no_img}}" class="user-camera"></router-link>
					<router-link to="/user_modify" class="user-edit"></router-link>
				</div>
				<div class="user-info-show">
					<h2>{{data.nick_name}}</h2>
					<!-- <p><mt-button size="small" type="primary" @click="loginOut"></mt-button></p> -->
				</div>
			</div>
			<ul class="padding20 user-account">
				<li><h2>{{data.user_money}}</h2><p> </p></li>
				<li><h2>{{data.red_envelop}}</h2><p> </p></li>
				<li><h2>{{data.score}}</h2><p> </p></li>
				<li><h2>999.00</h2><p></p></li>
			</ul>
			<div class="box-space-top15">
				<div class="padding20-x">
					<h3 class="title-icon title-icon-userorder"><router-link :to="{path:'/order_list/'}"  class="pull-right txt-assistant"></router-link></h3>
					<ul class="user-order">
						<li><router-link :to="{path:'/order_list/',query:{typeId:1}}"  class="waiting-pay"></router-link></li>
						<li><router-link :to="{path:'/order_list/',query:{typeId:2}}"  class="booked"></router-link></li>
						<li><router-link :to="{path:'/order_list/',query:{typeId:3}}"  class="done"></router-link></li>
						<li><router-link :to="{path:'/order_list/',query:{typeId:4}}"  class="cancel"></router-link></li>
					</ul>
				</div>
			</div>
			<div class="box-space-top15">
				<ul class="list-rightarrow padding20-x list-user">
					<li><div class="title-icon user-info"><router-link to="/user_modify"></router-link></div ></li>
					<li><div class="title-icon user-favorate"><router-link to="/favorite"></router-link></div></li>
					<li><div class="title-icon user-friendlink"><router-link to="/order_list"></router-link></div></li>
				</ul>
				<ul class="list-rightarrow padding20-x list-user">
					<li><div class="title-icon user-info" @click="loginOut"><a href="javascript:void(0)" ></a></div ></li>
				</ul>
				<div class="sys-service padding20">
					<h1>400-403-4322</h1>
					<p>8:00 ~ 1800</p>
				</div>
			</div>
		</div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
export default {
	name:'user',
  	components: {
  	},
  	data () {
		return {
			tokenId:localStorage.getItem("tokenId"),
			data:[]
		}
	},
	mounted(){
		if(this.tokenId)
			this.getData()
		else
			this.goLogin();	
	},
	methods: {
		getData(){
			this.$http.get('/User',{headers: {"authorization": "Bearer " + this.tokenId}})
			.then(res=>{
				this.data = res.data;
			})
			.catch(err=>{
				console.log(err);
				if(err.response.status==401)
				{
					Toast({
						message: '',
						position: 'bottom',
						duration: 2000
					});
					
					setTimeout(() => {
						this.goLogin();
					}, 2000);
				}	
			})
		},
		goLogin()
		{
			localStorage.setItem("return_url","/user");
			this.$router.push('/Login')
		},
		loginOut(){
			MessageBox.confirm('?').then(action => {
				localStorage.removeItem("tokenId")
				this.$router.push('/')
			})
			.catch(cancel=>{
				console.log(cancel)
			})
		}
	}
}

</script>



