<template>
  <div>
      <col-title></col-title>
      		<div class="wrapbox padding-footer">
			<div id="item1" class="withdraw-operate">
				<div class="padding20">
					<table class="table-default">
						<tr>
							<th></th>
							<td><input type="text" placeholder="" v-model="userName"></td>
						</tr>
						<tr>
							<th></th>
							<td><input type="password" placeholder="" v-model="passWord"></td>
						</tr>
						<tr>
							<th></th>
							<td><input type="password" placeholder="" v-model="rePassWord"></td>
						</tr>
                        <tr>
							<th></th>
							<td>
								<input type="radio" id="radio-1-1" name="radio-1" value="" class="regular-checkbox" v-model="sex"/><label for="radio-1-1"><i></i></label>
								<input type="radio" id="radio-1-2" name="radio-1" value="" class="regular-checkbox"  checked="checked" v-model="sex"/><label for="radio-1-2"><i></i></label>
							</td>
						</tr>
						<tr>
							<th></th>
							<td><input type="tel" placeholder="" v-model="tel"></td>
						</tr>
						<tr>
                            <th></th>
							<td><mt-button type="primary" v-model="telCode" size="small" @click="getTelCode" :disabled="isDisabled"><em v-if="isDisabled">({{secNum}})</em></mt-button></td>
						</tr>
                        <tr>
                            <th></th>
							<td><input type="text" placeholder="" v-model="telCode"></td>
						</tr>
					</table>					
				</div>
				<div class="box-space-top15">
					<div class="sys-service padding20">
						<h1 class="txt-assistant"></h1>
						<p></p>
					</div>
				</div>
			</div>
		</div>
		<footer class="footer-bar">
			<div class="padding20-x">
				<button class="btn-lg btn-ok" @click="subReg"></button>
			</div>
		</footer>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { Toast } from 'mint-ui'
	export default {
		name:'register',
		components: {
			colTitle
		},
		data () {
			return {
				userName:'',
				passWord:'',
				rePassWord:'',
				sex:'',
				tel:'',
				telCode:'',
				isDisabled:false,
				secNum:30
			}
		},
		methods: {
			subReg(){
				// 
				if (this.userName=='') {
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
				}
				if (this.passWord=='') {
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
				}else{
					if (this.passWord!=this.rePassWord) {
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
					}
				}
				if (this.tel=='') {
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
				}else{
					let pattern = /^1[34578]\d{9}$/;
					if (!pattern.test(this.tel)) {
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
					}
				}
				if (this.telCode=='') {
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
				}
				this.$http.post('/User/Register',{
					"name": this.userName,
					"pwd":  this.passWord,
					"tel": 	this.tel,
					"sex": 	this.sex,
					"code": this.telCode
				})
				.then(res=>{
					console.log(res)
					Toast({
						message: '',
						position: 'center',
						duration: 3000
					})
					this.userName = '',
					this.passWord = '',
					this.rePassWord = '',
					this.sex = '',
					this.tel = '',
					this.telCode = ''
					this.$router.push('/login')
				})
				.catch(err=>{
					console.log(err)
				})
			},
			// 
			getTelCode(){
				if (this.tel=='') {					// 
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
				}else{
					let pattern = /^1[34578]\d{9}$/;
					if (!pattern.test(this.tel)) {
					Toast({
						message: '',
						position: 'top',
						duration: 2000
					});
					return false
					}
				}
				this.isDisabled = true;
				let	timer1=setInterval(()=>{
						this.secNum>=0?this.secNum--:this.secNum=0
					},1000)
					setTimeout(() => {
						this.isDisabled = false
						clearTimeout(timer1)
						this.secNum = 30
					}, 30000);
				this.$http.post('/User/Sms',{
					tel:this.tel
				})
				.then(res=>{
					console.log(res)
					this.telCode = res.data.code
					Toast({
						message: ''+this.telCode,
						position: 'center',
						duration: 3000
					});
				})
			}
		}
	}
</script>
<style scoped>
.table-default th{text-align: right;padding-right: 15px}
.table-default td em{font-style: normal}
</style>



